import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getters'
import * as mutations from './mutations'
import * as actions from './actions'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    uploadFileType: '',
    commentId: '',
    userState: '',
    commentType: '',
    replyId: '',
    parentId: '',
    userId: '',
    userType: '',
    lessonType: '普课',
    lessonDate: '',
    lessonTime: '',
    userClick: '',
    videoInfo: {},
    normalLesson: [],
    personalLesson: [],
    lessonEdit: [],
    articleId: '',
    articleType: '',
    lessonId: '',
    appiontId: ''
  },
  getters,
  mutations,
  actions
})



