import axios from 'axios'

// axios 设置
axios.defaults.timeout = 100000 // 设置超时时间
axios.defaults.baseURL = 'https://henghasports.com'
// axios.defaults.baseURL = 'http://172.16.1.163:8088'

// 拦截接口返回异常跳转至登录页
axios.interceptors.response.use(
  response => {
    if (response.status === 403) {
      this.$router.push({
        path: '/'
      })
    }
    return response
  },
  error => Promise.reject(error.response.data)
)

export default axios

/**
 * fetch get
 * @param url
 * @param params
 * @returns {Promise<any>}
 */
export async function fetch (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    })
      .then(response => {
        resolve(response.data)
      }, error => reject(error))
  })
}

/**
 * Post
 * @param url
 * @param data
 * @returns {Promise<any>}
 */
export function post (url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, data)
      .then(response => resolve(response.data))
      .catch(error => reject(error))
  })
}

/**
 * put
 * @param url
 * @param data
 * @returns {Promise<any>}
 */
export function put (url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.put(url, data)
      .then(response => resolve(response.data))
      .catch(error => reject(error))
  })
}

export function deletes (url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.delete(url, data)
      .then(response => resolve(response.data))
      .catch(error => reject(error))
  })
}



