<template>
    <div class="footer">
        <div class="main">
            <router-view></router-view>
        </div>
        <ul class="bottomNav">
            <router-link tag="li" to="/home/index" class="navItem">
                <img class="icon" :src="'/home/index' === $route.path ? tabBarImgs[0].selected : tabBarImgs[0].normal">
                <span class="navLink">首页</span>
            </router-link>
            <router-link tag="li" to="/home/community" class="navItem">
                <img class="icon" :src="'/home/community' === $route.path ? tabBarImgs[1].selected : tabBarImgs[1].normal">
                <span class="navLink">论道</span>
            </router-link>
            <router-link tag="li" to="/home/prictice" class="navItem">
                <div class="circleDiv">
                    <img class="icon" :src="'/home/prictice' === $route.path ? tabBarImgs[2].selected : tabBarImgs[2].normal">
                </div>
                <span class="icon"></span>
                <span class="navLink">修炼</span>
            </router-link>
            <router-link tag="li" to="/home/teachers" class="navItem">
                <img class="icon" :src="'/home/teachers' === $route.path ? tabBarImgs[3].selected : tabBarImgs[3].normal">
                <span class="navLink">拜师</span>
            </router-link>
            <router-link tag="li" :to="isLogin === 'isLogin' ? '/home/mine' : '/home/login'" class="navItem">
                <img class="icon" :src="'/home/mine' === $route.path ? tabBarImgs[4].selected : tabBarImgs[4].normal">
                <span class="navLink">我的</span>
            </router-link>
        </ul>
    </div>
</template>

<script>
// import { getCookie } from '../../static/js/cookieUtil'
export default {
  name: 'BottomNav',
  data () {
    return {
      tabBarImgs: [{
        normal: require('../assets/img/home.png'), selected: require('../assets/img/homeActive.png')
      }, {
        normal: require('../assets/img/community.png'), selected: require('../assets/img/communityActive.png')
      }, {
        normal: require('../assets/img/prictice.png'), selected: require('../assets/img/pricticeActive.png')
      }, {
        normal: require('../assets/img/teacher.png'), selected: require('../assets/img/teacherActive.png')
      }, {
        normal: require('../assets/img/mine.png'), selected: require('../assets/img/mineActive.png')
      }],
      isLogin: ''
    }
  },
  mounted () {
    // this.isLogin = getCookie('userState')
    this.isLogin = localStorage.getItem('userState')
  }
}
</script>

<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    .bottomNav {
        display: flex;
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 1.125rem;
        background: #fffffe;
        border-top: 1px solid #f5f5f5;
        padding-top: 0.15rem;
        z-index: 9999;
    }
    .navItem {
        list-style: none;
        flex-grow: 1;
        font-weight: 500;
        font-size: 0.3rem;
    }
    .navItem.router-link-active {
        color: #ffd200;
    }
    .icon {
        display: block;
        width: 0.6rem;
        height: 0.6rem;
        margin: 0 auto;
    }
    .circleDiv {
        width: 1.125rem;
        height: 1.125rem;
        border-radius: 50%;
        position: absolute;
        top: -0.55rem;
        left: calc(50% - 0.65rem);
        background: #fff;
        border-top: 1px solid #f5f5f5;
        padding: 0.1rem;
    }
    .circleDiv .icon {
        display: inline-block;
        line-height: 1.5rem;
        width: 100%;
        height: 100%;
    }
    .navLink {
        display: inline-block;
        margin-top: 0.05rem;
    }
</style>



